


































.org {
  display: grid;
  grid-gap: 5rem 2rem;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  margin: 2rem 0;
}

.department {
  &__name {
    margin-bottom: 1rem;
    padding: 1rem 0;
    font-size: 1.25rem;
    border-top: 5px solid rgba(black, 0.1);
    border-bottom: 1px solid rgba(black, 0.1);
  }
}

.product {
  &__name {
    position: relative;
    margin: 0 -0.25rem;
    padding: 0.25rem;
    font-size: 1.25rem;

    &:hover {
      background: #f4f4f4;
    }

    &::after {
      position: absolute;
      top: 0;
      right: 0.5rem;
      display: block;
      font-family: Arial, Helvetica, sans-serif;
      content: 'â†’';
    }
  }
}
