











































@import '../styles/_colors.scss';

.header {
  width: 100%;
  height: 5rem;

  &.dev {
    background: #eeeeee;
  }
}

.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;

  h2 {
    margin: 0;
  }

  .right {
    margin-left: auto;
  }

  .menu-item {
    display: inline-block;
    margin: 0 0.25rem;
    padding: 0.55rem 0.75rem;
  }
}
