












































































.uploading {
  opacity: 0.2;
}

.image-uploader {
  transform: translate(-6px, 16.5rem);
}

.product-header__name {
  margin: -1rem -1rem 0;
  padding: 1rem 1rem 0;

  .title-1 {
    &::after {
      bottom: 0.4rem;
      display: inline-flex;
      align-content: center;
      align-items: center;
      margin-left: 1.5rem;
      padding: 0.4rem 0.75rem 0.25rem;
      font-weight: normal;
      font-size: 1rem;
      border: 1px solid white;
      transform: translateY(-0.25rem);
      opacity: 0.45;
      content: 'Endre';
    }
  }

  &:hover {
    background: rgba(white, 0.2);

    .title-1::after {
      opacity: 0;
    }
  }
}

