































































.objective {
  display: flex;
  align-items: center;

  &__select {
    min-width: 200px;
    margin-right: 1rem;
    transform: translateY(-3px);
  }
}

.add {
  position: relative;
  margin: 2rem 0;
}

.grid-animation-item {
  display: inline-block;
  margin-right: 10px;
  transform: scaleX(1);
  transition: all 0.5s;
}
.grid-animation-enter, .grid-animation-leave-to
/* .list-complete-leave-active below version 2.1.8 */ {
  transform: scaleX(0);
  opacity: 0;
}
.grid-animation-leave-active {
  position: absolute;
}
